<template lang="pug">
#Service
    mu-row( gutter )
        mu-col( v-for="item in serviceArr" class="serviceBox" width="100" tablet="50" desktop="20" v-bind:key="item.title" )
            div
                .imgBox
                    img( v-bind:src=" item.img " )
                h2 {{ item.title }}
                .string
                p( v-for="itemText in item.textArr" v-bind:style="{ display: 'block' }" ) {{ itemText.text }}
                .btnBox
                    //  'CMMI' router
                    router-link( v-if=" item.url != 'CMMI' " v-bind:to="item.url" )
                        mu-flat-button.demo-flat-button( label="" )
                    a( v-else href="http://baike.baidu.com/link?url=fEHKyvDNi26xfAcmLZmshqPUXjv_N100RQnBraaK2ACYZnJ_My57DTN_XHER-3q9dFkSySDl4h97xgipV6hou_" )
                        mu-flat-button.demo-flat-button( label="" )
                    
</template>

<script>
export default {
    name: 'Service',
    data() {
        return {
            serviceArr: [
                {
                    img     : require('../../assets/img/ico-tool-30-px@3x.png'),
                    title   : 'app',
                    url     : 'Application',
                    textArr: [
                        {
                            text: `AndroidiOS`
                        }, {
                            text: `WindowsPhoneHTML5`
                        }, {
                            text: `...`
                        }
                    ]
                }, {
                    img     : require('../../assets/img/ico-software-30-px@3x.png'),
                    title   : '',
                    url     : 'BigData',
                    textArr: [
                        {
                            text: ``
                        }, {
                            text: ``
                        }, {
                            text: `...`
                        }
                    ]
                }, {
                    img     : require('../../assets/img/ico-manager-30-px@3x.png'),
                    title   : '',
                    url     : 'ECommerce',
                    textArr: [
                        {
                            text: ``
                        }, {
                            text: ``
                        }, {
                            text: `B2CB2B...`
                        }
                    ]
                }, {
                    img     : require('../../assets/img/ico-software-30-px@3x.png'),
                    title   : '',
                    url     : 'SystemIntegration',
                    textArr: [
                        {
                            text: ``
                        }, {
                            text: `RFID`
                        }, {
                            text: ``
                        }
                    ]
                }, {
                    img     : require('../../assets/img/ico-c-m-m-i-30-px@3x.png'),
                    title   : 'CMMI',
                    url     : 'CMMI',
                    textArr: [
                        {
                            text: `CMMI`
                        }, {
                            text: ``
                        }, {
                            text: ``
                        }
                    ]
                }
            ]
        }
    }
}
</script>

<style lang="sass?indentedSyntax" scoped>
@import '../../sass/main'

#Service
    +moduleBoxStyle
    .serviceBox
        +flexCenter
        +textCenter
        +REM( padding, $M-padding )
        +REM( margin-bottom, $M-padding )
        +bC( $F )
        >div
            .imgBox
                @extend %dib
                +REM-W-H( 30px, 30px )
                >img
                    +imgCover( 100% )
            h2
                +REM( font-size, $F-title*.7 )
                +REM( padding-top, $M-contentMargin )
                +REM( padding-bottom, $M-contentMargin )
            .string
                @extend %dib
                width: 30%
                +REM( height, 2px )
                +REM( margin-bottom, $M-contentMargin )
                +bC( $C-theme )
            p
                +REM( font-size, $F-text*.6 )
                +REM( line-height, $F-title )
                color: $C-text
            // "" 
            .demo-flat-button
                +ellipseBtn( 30px )
                width: auto
                margin: 0
                +REM-margin-TB( $M-margin )
                box-shadow: none                        // shadow
                border: 1px solid rgba( $C-text, .4 )
                color: $C-text
                // hover 
                &:hover
                    border-color: $C-theme
                    +bC( $C-theme )
                    color: $F
        @media only screen and ( min-width : 768px )
            height: 360px
        @media only screen and ( min-width : 1024px )
            height: 460px
            >div
                +REL
                +pT( $M-padding*2 )
                height: 100%
                >.btnBox
                    +ABS
                    +flexCenter
                    bottom: 0
                    +W100
    @media only screen and ( min-width : 1180px )
        max-width: 1180px
        margin: 0 auto
        padding:
            left: 0
            right: 0
</style>



